<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.5.1/jquery.min.js"></script>

<script>
import $ from "jquery";

import VueSlickCarousel from "vue-slick-carousel";
export default {
  components: {VueSlickCarousel},

  data() {
    return {
      number: 1,
      slickOptions: {
        slidesToShow: 1,
        infinite: true,
        accessibility: true,
        adaptiveHeight: false,
        arrows: false,
        dots: true,
        // autoplay: true,
        // autoplaySpeed: 6000,
        draggable: true,
        edgeFriction: 0.3,
        swipe: true,
      },
    };
  },
  methods: {
    handleAfterChange(currentSlide) {
      this.number = currentSlide + 1;
    },
  },
  mounted() {
    $(window).on("load", function() {
      $(".pre-load").show();
      setTimeout(function() {
        $(".pre-load").fadeOut();
      }, 2500);
    });
    $(window).scroll(function() {
      if ($(window).scrollTop()) {
        $(".header").addClass("header-bg");
      } else {
        $(".header").removeClass("header-bg");
      }
    });
    $(".header-menu").click(function() {
      $(".nav-mobile").addClass("nav-mobile-visible");
    });
    $(".header-exits").click(function() {
      $(".nav-mobile").removeClass("nav-mobile-visible");
    });
    scroll = function(className, scrollToEl) {
      $(className).click(function() {
        $(".pre-load").show();
        setTimeout(function() {
          $(".pre-load").fadeOut();
        }, 1000);
        $("html, body").animate(
          {
            scrollTop: $(scrollToEl).offset().top - 80,
          },
          0
        );
      });
    };
    scroll(".homeClick", ".home");
    scroll(".ourStoryClick", ".our-story");
    scroll(".shopClick", ".shop-container");
    scroll(".ourServicesClick", ".our-service");
    scroll(".aboutUsClick", ".about");

    scroll(".header-read-click", ".our-story");
    scroll(".header-buy-click", ".shop");

    $(".contactClick").click(function() {
      if ($(window).width() > 1199) {
        $(".pre-load").show();
        setTimeout(function() {
          $(".pre-load").fadeOut();
        }, 1000);
        $("html, body").animate(
          {
            scrollTop: $(".contact").offset().top + 20,
          },
          0
        );
      } else {
        $(".pre-load").show();
        setTimeout(function() {
          $(".pre-load").fadeOut();
        }, 1000);
        $("html, body").animate(
          {
            scrollTop: $(".contact").offset().top - 80,
          },
          0
        );
      }
    });
  },
};
</script>

<style lang="scss">
@import "../assets/style/_mixin.scss";

@import url("https://fonts.googleapis.com/css2?family=Pacifico&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&family=Pacifico&display=swap");

.home {
  .pre-load {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgb(8, 8, 8);
    z-index: 999999999999;
    display: flex;
    align-items: center;
    justify-content: center;
    div {
      @include respond(576px) {
        transform: translateY(-20px);
      }
      span {
        padding: 6px;
        border-radius: 50%;
        font-size: 0;
        display: inline-block;
        @include respond(576px) {
          padding: 4px;
        }
        &:first-child {
          background: rgb(135, 230, 126);
          animation: dot1 2s linear infinite;
        }
        &:nth-child(2) {
          background: rgb(211, 220, 30);
          margin: 0 10px;
          animation: dot2 2s linear infinite;
        }
        &:last-child {
          background: rgb(245, 127, 48);
          animation: dot3 2s linear infinite;
        }
      }
    }
  }
  @keyframes dot1 {
    0% {
      transform: translateY(0);
      opacity: 0.7;
    }
    25% {
      transform: translateY(-3px);
      opacity: 1;
    }
    50% {
      transform: translateY(0);
      opacity: 0.7;
    }
    75% {
      transform: translateY(0);
      opacity: 0.7;
    }
    100% {
      transform: translateY(0);
      opacity: 0.7;
    }
  }
  @keyframes dot2 {
    0% {
      transform: translateY(0);
      opacity: 0.7;
    }
    25% {
      transform: translateY(0);
      opacity: 0.7;
    }
    50% {
      transform: translateY(-3px);
      opacity: 1;
    }
    75% {
      transform: translateY(0);
      opacity: 0.7;
    }
    100% {
      transform: translateY(0);
      opacity: 0.7;
    }
  }
  @keyframes dot3 {
    0% {
      transform: translateY(0);
      opacity: 0.7;
    }
    25% {
      transform: translateY(0);
      opacity: 0.7;
    }
    50% {
      transform: translateY(0);
      opacity: 0.7;
