      a {
        color: #c3a281;
        position: relative;
        transition: 0.4s;
        &::after {
          content: "";
          position: absolute;
          width: 0;
          border-top: 2px solid #c3a281;
          bottom: -3px;
          right: 0;
          transition: 0.4s;
        }
        @include respondMIn(1024px) {
          &:hover {
            color: white;
            &:after {
