          font-size: 30px;
        }
      }
      @include respondText(992px, 70px) {
        p {
          font-size: 25px;
        }
      }
      @include respondText(768px, 50px) {
        p {
          font-size: 18px;
        }
      }
      @include respondText(577px, 40px);
