<script>
import $ from "jquery";
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";

export default {
  components: { VueSlickCarousel },
  data() {
    return {
      number: 1,
      slickOptions: {
        slidesToShow: 1,
        infinite: true,
        accessibility: true,
        adaptiveHeight: false,
        arrows: true,
        dots: true,
        autoplay: true,
        draggable: true,
        edgeFriction: 0.3,
        swipe: true,
         responsive: [
                          
                          {
                              breakpoint: 767,
                              settings: {
                                autoplay: false
                              }
                            }
                          ]
      },
    };
  },
  methods: {
    handleAfterChange(currentSlide) {
      this.number = currentSlide + 1;
    },
  },
  mounted() {
    if ($(window).width() > 767) {
      $(".slick-prev").html("<");
      $(".slick-next").html(">");
    }
    scroll(".shopClick", ".shop-container");
    scroll(".shop1Click", ".shop1");
    scroll(".shop2Click", ".shop2");
  },
 
};
</script>

<style lang="scss">
/** @format */

@import "../../assets/style/_mixin.scss";

.shop-list-items {
  color: #53494a;
  font-size: 25px;
  li:nth-child(2) {
    margin: 0 100px;
  }
  @include respond(991px) {
    font-size: 20px;
    li:nth-child(2) {
      margin: 0 60px;
    }
  }
  @include respond(576px) {
    font-size: 18px;
    li:nth-child(2) {
      margin: 0 40px;
    }
  }
  a {
    position: relative;
    transition: 0.3s;
    &::after {
      bottom: -5px;
      left: 0;
      content: "";
      position: absolute;
      width: 100%;
      border-top: 2px solid #c3a281;
      transform: scale(0);
      transition: 0.3s;
    }
    @include respondMIn(1024px) {
      &:hover {
        color: #c3a281;
        &::after {
          transform: scale(1.4);

        }
      }
    }
  }

  .active {
    color: #c3a281;
    &::after {
      transform: scale(1.4);
    }
  }
}

.shop {
  .slider-number {
    text-align: center;
    font-size: 21px;
    color: white;
    padding-bottom: 5px;
    @include respond(991px) {
      font-size: 17px;
    }
    @include respond(576px) {
      font-size: 15px;
    }
  }
  .slider-shop {
    position: relative;
    padding: 50px 0;
    @include respond(576px) {
      padding: 20px 0;
    }
  }
  .slick-prev,
  .slick-next {
    background: #c3a281;
    color: white;
    height: 140px;
    padding: 0 8px;
    font-size: 40px;
    width: auto;
    position: absolute;
    z-index: 1;
    &::before {
      content: "";
    }
    @include respond(1199px) {
      font-size: 30px;
      height: 120px;
    }
    @include respond(767px) {
      display: none !important;
    }
  }
  .slick-prev {
    left: 0;
  }
  .slick-next {
    right: 0;
  }
  .slick-dots {
    @include slick-dots;
    bottom: -55px;
    li {
      button {
        &::before {
          background: #53494a;
        }
      }
    }
    @include respond(991px) {
      bottom: -45px;
    }
    @include respond(576px) {
      bottom: -40px;
    }
  }
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../../assets/img/shop/shop_bg.jpg");
  background-size: cover;
  background-position: center;
  &-left {
    div {
      width: 50%;
      &:first-child {
        padding: 0 15px 15px 0;
        @include respond(576px) {
          padding: 0 5px 5px 0;
        }
      }
      &:nth-child(2) {
        padding: 0 0 15px 15px;
        @include respond(576px) {
          padding: 0 0 5px 5px;
        }
      }
      &:nth-child(3) {
        padding: 15px 15px 0 0;
        @include respond(576px) {
          padding: 5px 5px 0 0;
        }
      }
      &:last-child {
        padding: 15px 0 0 15px;
        @include respond(576px) {
          padding: 5px 0 0 5px;
        }
      }
      a {
        overflow: hidden;
        display: block;
        position: relative;
        div {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
          position: absolute;
          background: rgba(0, 0, 0, 0.5);
          color: white;
          font-size: 50px;
          top: 0;
          left: 0;
          visibility: hidden;
          opacity: 0;
          transition: 0.4s;
        }
        @include respondMIn(1024px) {
          &:hover {
            div {
              visibility: visible;
              opacity: 1;
            }
            img {
              transform: scale(1.1);
              background-color: rgba(0, 0, 0, 0.5) !important;
            }
          }
        }
      }
    }
  }
  &-right {
    a {
      position: relative;
      overflow: hidden;
      display: block;
      @include respondMIn(1024px) {
        &:hover {
          img {
            transform: scale(1.1);
            background-color: rgba(0, 0, 0, 0.5) !important;
          }
          .text {
            background: rgba(0, 0, 0, 0.5);
            span {
              font-size: 110px;
              background: none;
            }
          }
        }
      }
    }
    .text {
      transition: 0.4s;
      position: absolute;
      width: 100%;

      top: 0;
      height: 100%;

      span {
        transition: 0.4s;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 80px;
        background: #c3a281;
        color: white;
        width: 80px;
        height: 80px;
        text-align: center;
        line-height: 80px;
        font-weight: 300;
      }
      div {
        transition: 0.4s;
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, 80%);
        text-align: center;
        p {
          color: white;
          font-size: 1.5rem;
        }
        button {
          @include btn(160px, #c3a281);
          @include btn-hover (#c3a281, white, white);
          background: white;
          font-size: 1.2rem;
        }
      }
      @include respond(1200px) {
        div {
          transform: translate(-50%, 50%);
          button {
            padding: 5px 20px;
            font-size: 1.2rem;
          }
        }
      }
      @include respond(991px) {
        @media (min-width: 767px) {
          span {
            width: 50px;
            height: 50px;
            font-size: 50px;
            line-height: 50px;
          }
          div {
            transform: translate(-50%, 40%);
            p {
              font-size: 20px;
            }
            button {
              padding: 5px 10px;
            }
          }
        }
      }
      @include respond(420px) {
        span {
          width: 50px;
          height: 50px;
          font-size: 50px;
          line-height: 50px;
        }
        div {
          transform: translate(-50%, 40%);
          p {
            font-size: 16px;
          }
          button {
            width: 140px;
            font-size: 15px;

            height: calc(140px / 4);
          }
        }
      }
    }
    @include respond(767px) {
      margin-top: 30px;
    }
    @include respond(576px) {
      margin-top: 10px;
    }
  }
}
</style>
