<script>
import VueSlickCarousel from "vue-slick-carousel";
import "vue-slick-carousel/dist/vue-slick-carousel.css";
// optional style for arrows & dots
import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";

export default {
  components: {VueSlickCarousel},
  data() {
    return {
      activeColor: "",
      width: "",
      margin: "",
      slickOptions: {
        slidesToShow: 1,
        infinite: true,
        accessibility: true,
        adaptiveHeight: false,
        arrows: false,
        dots: true,
        autoplay: true,
        draggable: true,
        edgeFriction: 0.3,
        swipe: true,
      },
    };
  },

  mounted() {
    this.width = this.$refs.container.offsetLeft;
    this.margin = this.width + 15;
    scroll(".about1Click", ".about-details");
    scroll(".about2Click", ".about-details2");
  },
  created() {
    window.addEventListener("resize", this.myEventHandler);
  },
  destroyed() {
    window.removeEventListener("resize", this.myEventHandler);
  },
  methods: {
    myEventHandler() {
      this.activeColor = "blue";
      return (this.margin = this.$refs.container.offsetLeft + 15);
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/style/_mixin.scss";

.about {
  .slick-dots {
    z-index: 1000;
    @include slick-dots;
    bottom: -8%;
    @include respondMIn(768px) {
      padding-right: 15px;
    }
    li {
      button {
        background: #f7ede4;
      }
    }
    @include respond(576px) {
      bottom: -20px;
    }
  }
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../../assets/img/about/aboutBg.jpg");
  background-size: cover;
  background-position: center;
  padding: 70px 0;
  &-container {
    @include respond(767px) {
      padding-left: 0 !important;
    }
  }
  .img-height {
    height: 100%;
  }
  .about-info {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100%;
    padding: 40px 0;
    background: #c3a281;
    color: white;
    button {
      color: white;
      background: transparent;
      border: 2px solid white;
      height: 50px;
      width: 50px;
      font-size: 20px;
      transition: 0.6s;
      @include respondMIn(1024px) {
        &:hover {
          border: 2px solid rgb(0, 0, 0);
          color: black;
        }
      }
    }
    span {
      font-size: 25px;
      line-height: 25px;
