      }
    }
    @include respond(1199px) {
      button {
        height: 40px;
        width: 40px;
        font-size: 18px;
      }
      span {
        font-size: 20px;
        p {
          font-size: 15px;
        }
      }
    }
    @include respond(991px) {
      @include respondMIn(768px) {
        padding: 30px 0;
        button {
          height: 30px;
          width: 30px;
          font-size: 16px;
        }
        .btn-btn {
          width: 140px !important;
        }
        span {
          font-size: 16px;
          line-height: 18px;
          p {
            font-size: 14px;
          }
        }
      }
    }
    .btn-btn {
      width: 180px;
    }
    @include respond(576px) {
      padding: 25px 0;
    }
    @include respond(500px) {
      padding: 15px 0;
      button {
        margin-left: 5px !important;
        width: 28px;
        height: 28px;
        font-size: 14px;
        line-height: 14px;
      }
      .btn-btn {
        width: 120px !important;
        margin-left: 0 !important;
      }
      span {
        font-size: 14px;
        line-height: 16px;
        p {
          font-size: 13px;
        }
      }
    }
  }
  .title {
    padding-right: 15%;
    @include respond(500px) {
      padding: 0 !important;
      p {
        font-size: 14px !important;
      }
    }
    p {
      color: white;
      font-size: 21px;
    }
    span {
      font-size: 46px;
      position: relative;
      color: #c3a281;
      &:after {
        position: absolute;
        content: "";
        width: 250%;
        left: 0;
        bottom: -10px;
        border-top: 2px solid #c3a281;
      }
    }
    @include respond(1199px) {
      padding-right: 10%;
      span {
        font-size: 35px;
        &::after {
          width: 200%;
        }
      }
      p {
        font-size: 18px;
      }
    }
    @include respond(991px) {
      padding-right: 15px;
      span {
        font-size: 30px;
      }
      p {
        font-size: 16px;
      }
    }
    @include respond(767px) {
      text-align: center;
      padding: 0 10%;
      span {
        &::after {
          width: 100%;
          transform: scale(1.2);
        }
      }
    }
  }
  @include respond(576px) {
