          visibility: visible;
          opacity: 1;
          font-size: 50px;
        }
        .play {
          visibility: hidden;
          opacity: 0;
        }
      }
    }
    .play-hover {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
      position: absolute;
      background: rgba(0, 0, 0, 0.5);
      color: white;
      font-size: 0;
      top: 0;
      left: 0;
      visibility: hidden;
      opacity: 0;
      transition: 0.4s;
    }
    .play {
      transition: 0.4s;
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100px;
      height: 100px;
      transform: translate(-50%, -50%);
      background: white;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      @include respond(500px) {
        width: 50px;
        height: 50px;
      }
