<script>

export default {

  data() {
    return {
      activeColor: "",
      width: "",
      margin: "",
    };
  },
  mounted() {
    this.width = this.$refs.container.offsetLeft;
    this.margin = this.width + 15;
  },
  created() {
    window.addEventListener("resize", this.myEventHandler);
  },
  destroyed() {
    window.removeEventListener("resize", this.myEventHandler);
  },
  methods: {
    myEventHandler() {
      return (this.margin = this.$refs.container.offsetLeft + 15);
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/style/_mixin.scss";
.shop-details {
  .shop-box {
    position: relative;
    &:last-child {
      .shop-bg {
        left: 0;
        transform: scaleX(-1);
        top: 0;
      }
    }
  }
  .shop-bg {
    position: absolute;
    right: 0;
    width: 17%;
    top: -20%;
    z-index: -1;
    @include respond(991px) {
      top: -10%;
      width: 25%;
    }
  }
  .shop-right-content {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }
  input,
  button {
    border: 2px solid #c3a281;
    border-radius: 0;
    flex: 1;
  }
  button {
    background: transparent;
    transition: 0.4s;
    &:hover {
     background: rgb(158, 158, 199);
    }
  }
  .title {
    text-align: center;
    span {
      position: relative;
      color: #c3a281;
      font-size: 25px;
      &::after {
        position: absolute;
        content: "";
        width: 100%;
        transform: scale(1.05);
        border-top: 2px solid #c3a281;
        left: 0;
        bottom: -10px;
      }
    }

    p {
      margin-top: 20px;
      text-align: justify;
    }
    @include respond(992px) {
      span {
        font-size: 20px;
      }
      p {
        margin-top: 15px;
      }
    }
    @include respond(767px) {
      span {
        font-size: 15px;
        &::after {
          bottom: -5px;
        }
      }
      p {
        font-size: 14px;
        margin-top: 10px;
      }
    }
    @include respond(576px) {
      span {
        font-size: 1.3rem;
      }
    }
  }
  &-right {
    span {
      font-size: 28px;
      color: #c3a281;
    }
  }
  @include respond(576px) {
    .shop-title {
      span {
        font-size: 25px;
      }
    }
    .row {
      margin-left: 0px !important;
      margin-right: 0px !important ;
    }
    .shop-container {
      padding-left: 10px !important;
      padding-right: 10px !important;
    }
    .shop-right-img {
      div {
        margin-top: 20px;
        padding: 0 !important;
      }
    }
    .shop-bg {
      top: -60px;
    }
  }
  @include respond(420px) {
    .shop-container {
      padding-left: 0 !important ;
      padding-right: 0 !important;
    }
    .shop-right-img {
      margin-top: 20px;
      div {
        margin-top: 10px;
      }
    }
  }
}
</style>
