import AboutUsDetail from './components/aboutUs/AboutUsDetail.vue'
import Menu from './components/menu/Menu.vue'
import OurStoryDetail from './components/ourStory/OurStoryDetail.vue'
import OurServicesDetail from './components/ourServices/OurServicesDetail'
import Contact from './components/home/Contact.vue'
import Home from './components/home/Home.vue'
export const routes = [
    {
        path : '/menu',
        name : 'menu',
        component : Menu
    },
    {
        path : '/menu/:tag',
        name : 'menu-lorem',
        component : Menu
    },
    {
        path : '/about-us',
        name : 'about-us',
        component : AboutUsDetail
      },
      {
        path : '/about-us/:tag',
        name : 'about-us-lorem',
        component : AboutUsDetail
      },
      {
          path : '/our-story',
          name : 'our-story',
          component : OurStoryDetail
      },
    
      {
        path : '/our-services',
        name : 'our-services',
        component : OurServicesDetail
    },
    {
        path : '/our-services/:tag',
        name : 'our-services-lorem',
        component : OurServicesDetail
    },
    {
        path : '/contact',
        name : 'contact',
        component : Contact
    },
    {
        path : '/',
        name : 'home',
        component : Home
    },
    { path: '*', redirect: '/' }

 
]